</template>
<script>
import axios from "axios";
// import InFoModel from '../components/InFoModel.vue';
export default {
    name: "MineView",
    components:{
        // InFoModel
    },
    data() {
        return {
            changename:null,
            changetext:null,
            changetext1:null,
            change:null,
            userdetails: {
                id: null,
                nickname: null,
                username: null,
                balance: 0,
                usercode: null,
                commision: "0",
                intrest: "0",
                notice: "Welcome",
                

            },
            invite:{
                agentincome:0,
            },
            wag:{
                amount:0,
            },
            user: [],
            dailysign: [],
            isModalVisible: false,
        }

    },
    beforeCreate: function () {
        if (localStorage.getItem('username') == null) {
            this.$router.push({ name: 'login' });
        }

    },
    created: function () {
        this.getUserdetails();
        this.getrecord();
        this.getWagAmount();


    },
    


    methods: {
        pop(message) {
            document.getElementById("msg").innerHTML = message;
            document.getElementById("msgbox").style.display = "";
            setTimeout(this.getUserdetails(),1500);
        },
        changebox(name){
            document.getElementById("clink").style.display = "";
            document.getElementById("change").style.display = "";
            if(name=='nickname'){
                this.changename='nickname';
                this.changetext='Change Nick Name';
                this.changetext1='Enter Nick Name';
            }else if(name=='password'){  
                this.changename='password';
                this.changetext='Set New Password';
                this.changetext1='New Password';
            }
        },
        cancel() {
            document.getElementById("clink").style.display = "none";
            this.change = null;
        },
        cancel1() {
            document.getElementById("clink1").style.display = "none";
        },
        clink1() {
            document.getElementById("clink1").style.display = "";
        },
        confirmchange(){
            
            if(this.changename=='nickname'){
                if(this.change==null){
                    this.pop("Enter Your Name");
                    this.cancel();
                }else{
                    axios.get('https://smarthubb.store/gameapi/src/api/bet.php?action=changenickname&user=' + this.userdetails.username +'&nickname='+this.change);
                  
                    this.pop("Successfully");
                    this.cancel();
                    
                }
            }else if(this.changename=='password'){
                if(this.change==null){
                    this.pop("Please Enter Password");
                    this.cancel();
                }else{
                    axios.get('https://smarthubb.store/gameapi/src/api/bet.php?action=changepassword&user=' + this.userdetails.username +'&password='+this.change);
                    
                    this.pop("Successfully");
                    this.cancel();
                }
            }
        },
        remove(){
            document.getElementById("msgbox").style.display = "none";
            this.getUserdetails();
        },
        logout() {
            localStorage.removeItem('username')
            this.$router.push("/login");
        },
        showModal() {
            this.isModalVisible = true;
           
        },
        closeModal() {
            this.isModalVisible = false;
        },
        getUserdetails() {

            this.userdetails.username = localStorage.getItem('username');
            axios.get('https://smarthubb.store/gameapi/src/api/me.php?action=getuserinfo&user=' + this.userdetails.username).then((res) => {
                this.Users = res.data.user_Data;
                this.userdetails.id = this.Users[0].id;
                this.userdetails.balance = this.Users[0].balance;
                this.userdetails.usercode = this.Users[0].usercode;
                this.userdetails.nickname = this.Users[0].nickname;
            }).catch((err) => {
                console.log(err);
            })
        },
        sendInvitation(){
            axios.post('https://smarthubb.store/gameapi/src/api/me.php?action=invite&user=' + this.userdetails.username).then((res) => {
                console.log(res)
                if(res.data.status == true){
                    window.location.href = 'https://t.me/playclube';
                }
            }).catch((err) => {
                console.log(err);
            })
        },
        getrecord() {
            this.userdetails.username = localStorage.getItem('username');

            axios.get('https://smarthubb.store/gameapi/src/api/me.php?action=getuserinfo&user=' + this.userdetails.username).then((res) => {
                this.info = res.data.user_Data[0];
                console.log(this.info);

            }).catch((err) => {
                console.log(err);
            })

            axios.get('https://smarthubb.store/gameapi/src/api/bet.php?action=invite&usercode=' + this.userdetails.username).then((res) => {
                this.invite = res.data[0];
                console.log(this.invite);
                if (this.invite.agentincome == 0) {
                    this.invite.agentincome = 0.00;
                }
            }).catch((err) => {
                console.log(err);
            })
        },
        getWagAmount(){
            axios.post('https://smarthubb.store/gameapi/src/api/me.php?action=get_wag_amount&user=' + this.userdetails.username).then((res) => {
                console.log(res)
                if(res.data.status == true){
                   this.wag.amount = res.data.data
                }
            }).catch((err) => {
                console.log(err);
            })
        },
    }

}
</script>